@font-face {
  font-weight: 400;
  font-family: "Jost";
  font-style: normal;
  src: url("../fonts/jost-400.ttf");
  font-display: swap;
}
@font-face {
  font-weight: 700;
  font-family: "Jost";
  font-style: bold;
  src: url("../fonts/jost-700.ttf");
  font-display: swap;
}
@font-face {
  font-weight: 400;
  font-family: "Forum";
  font-style: normal;
  src: url("../fonts/forum-400.ttf");
  font-display: swap;
}
@font-face {
  font-weight: 500;
  font-family: "Montserrat";
  font-style: normal;
  src: url("../fonts/montserrat-500.ttf");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  overflow: hidden;
  clip: rect(0 0 0 0);
}
* {
  margin: 0;
  padding: 0;
  border: none;
  -webkit-tap-highlight-color: transparent;
}
*,
:after,
:before {
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
}
img {
  display: block;
  max-width: 100%;
  height: auto;
}
ul {
  list-style-type: none;
  padding: 0;
  -webkit-padding-start: 0;
  padding-inline-start: 0;
}
input::-ms-clear {
  display: none;
}
a,
button {
  display: block;
  box-sizing: border-box;
  padding: 0;
  color: inherit;
  font: inherit;
  text-decoration: none;
  text-align: left;
  background-color: inherit;
  border: none;
  cursor: pointer;
}
.wrapper-width {
  width: 100%;
  margin-right: auto;
  margin-left: auto;
}
@media (min-width: 1400px) {
  .wrapper-width {
    max-width: 1920px;
  }
}
.wrapper-padding {
  padding-right: 20px;
  padding-left: 20px;
}
@media (min-width: 768px) {
  .wrapper-padding {
    padding-right: 30px;
    padding-left: 30px;
  }
}
@media (min-width: 1200px) {
  .wrapper-padding {
    padding-right: 64px;
    padding-left: 64px;
  }
}
@media (min-width: 1400px) {
  .wrapper-padding {
    max-width: 1400px;
    padding-right: 92px;
    padding-left: 92px;
  }
}
.wrapper {
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  padding-right: 20px;
  padding-left: 20px;
}
@media (min-width: 1400px) {
  .wrapper {
    max-width: 1920px;
  }
}
@media (min-width: 768px) {
  .wrapper {
    padding-right: 30px;
    padding-left: 30px;
  }
}
@media (min-width: 1200px) {
  .wrapper {
    padding-right: 64px;
    padding-left: 64px;
  }
}
@media (min-width: 1400px) {
  .wrapper {
    max-width: 1400px;
