.interactive-styles:hover {
  color: #bb8c5f;
}
.interactive-styles:focus-visible {
  color: #bb8c5f;
  outline: none;
}
.interactive-styles:active {
  color: #bb8c5f;
}
.interactive-styles-custom:hover {
  opacity: 0.7;
}
.interactive-styles-custom:focus-visible {
  opacity: 0.7;
