}
.interactive-styles-custom:active {
  opacity: 0.7;
}
@media (max-width: 1199.8px) {
  .blur {
    position: fixed;
    z-index: 10;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow-y: scroll;
    background-color: #030201;
    opacity: 0;
    pointer-events: none;
  }
  .blur--active {
    opacity: 0.6;
    pointer-events: auto;
  }
}
@media (max-width: 1199.8px) {
  .modal {
    position: fixed;
    top: 80px;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 99;
    pointer-events: none;
    visibility: hidden;
  }
}
.modal::-webkit-scrollbar-button {
  background-color: transparent;
}
@media (max-width: 1199.8px) {
  .modal--opened {
    pointer-events: auto;
    visibility: visible;
    -webkit-overflow-scrolling: touch;
  }
}
@media (max-width: 1199.8px) {
  .modal__wrapper {
    height: 100%;
    overflow-y: auto;
    transform: translateX(-100%);
    opacity: 0;
    transition: transform 0.5s, opacity 0.5s;
  }
}
@media (max-width: 1199.8px) {
  .modal--opened .modal__wrapper {
    transform: translateX(0);
    opacity: 1;
    transition: transform 0.5s, opacity 0.5s;
  }
}
.scroll {
  scrollbar-color: #bb8c5f #050402;
  scrollbar-width: thin;
}
.app-btn {
  position: relative;
  width: calc(320px - 2 * 20px);
  min-height: 70px;
  padding: 18px;
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  text-align: center;
  border: 2px solid #bb8c5f;
}
@media (min-width: 768px) {
  .app-btn {
    width: 245px;
  }
}
.app-btn--card {
  width: 100%;
  min-height: auto;
  padding: 8px;
}
.app-btn--dark {
  color: #faf6f2;
  background-color: #524336;
}
.app-btn--light {
  color: #bb8c5f;
  background-color: #eeeeee;
}
.app-btn:hover {
  color: #faf6f2;
  background-color: #bb8c5f;
}
.app-btn:focus-visible {
  color: #faf6f2;
  background-color: #bb8c5f;
  outline: none;
}
.app-btn__decor {
  position: absolute;
  top: 50%;
}
.app-btn__decor::before,
.app-btn__decor::after {
  position: absolute;
  content: "";
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 0;
  height: 0;
}
.app-btn__decor::before {
  width: 0;
  height: 0;
  border-top: calc(20px / 2) solid transparent;
  border-bottom: calc(20px / 2) solid transparent;
  border-left: 17px solid #bb8c5f;
}
.app-btn__decor--left {
  left: 0;
  transform: translateX(-100%) translateY(-50%) rotate(180deg);
}
.app-btn__decor--right {
  right: 0;
  transform: translateX(100%) translateY(-50%);
}
.app-btn--dark .app-btn__decor::after {
  width: 0;
  height: 0;
  border-top: calc(16px / 2) solid transparent;
  border-bottom: calc(16px / 2) solid transparent;
  border-left: 13px solid #524336;
}
.app-btn--light .app-btn__decor::after {
  width: 0;
  height: 0;
  border-top: calc(16px / 2) solid transparent;
  border-bottom: calc(16px / 2) solid transparent;
  border-left: 13px solid #eeeeee;
}
.app-btn:hover .app-btn__decor::after,
.app-btn:focus-visible .app-btn__decor::after {
  width: 0;
  height: 0;
  border-top: calc(16px / 2) solid transparent;
  border-bottom: calc(16px / 2) solid transparent;
  border-left: 13px solid #bb8c5f;
}
.pagination {
  display: flex;
  gap: 4px;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
@media (min-width: 1400px) {
  .pagination {
    gap: 6px;
  }
}
@media (min-width: 1200px) {
  .pagination--vertical {
    flex-direction: column;
  }
}
.pagination--disabled {
  display: none;
}
.pagination__item {
  display: flex;
}
.pagination__btn {
  flex-shrink: 0;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
}
.pagination__btn:focus-visible {
  outline: none;
}
.pagination__btn::before {
  position: absolute;
  content: "";
  width: 16px;
  height: 16px;
  border: 1px solid #524336;
}
.pagination__btn::after {
  position: absolute;
  content: "";
  width: 10px;
  height: 10px;
}
.pagination__btn--current::after {
  background-color: #bb8c5f;
}
.pagination__btn:not(.pagination__btn--current):hover::after,
.pagination__btn:not(.pagination__btn--current):focus-visible::after {
  background-color: #524336;
}
.arrow-btn {
  position: relative;
  width: 40px;
  height: 40px;
  color: #bb8c5f;
  background-color: #eeeeee;
  border: 1.5px solid #bb8c5f;
}
@media (min-width: 1400px) {
  .arrow-btn {
    width: 50px;
    height: 50px;
  }
}
.arrow-btn::before {
  position: absolute;
  content: "";
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  border-top: calc(12px / 2) solid transparent;
  border-bottom: calc(12px / 2) solid transparent;
  border-left: 10px solid #bb8c5f;
}
.arrow-btn--prev::before {
  transform: rotate(180deg) translateX(60%) translateY(50%);
}
.arrow-btn--next::before {
  transform: translateX(-30%) translateY(-50%);
}
.arrow-btn--disabled {
  opacity: 0.3;
  cursor: initial;
}
.arrow-btn:not(.arrow-btn--disabled):hover,
.arrow-btn:not(.arrow-btn--disabled):focus-visible {
  border-color: #524336;
  outline: none;
}
.arrow-btn:not(.arrow-btn--disabled):hover::before,
.arrow-btn:not(.arrow-btn--disabled):focus-visible::before {
  width: 0;
  height: 0;
  border-top: calc(12px / 2) solid transparent;
  border-bottom: calc(12px / 2) solid transparent;
  border-left: 10px solid #524336;
}
