.page-footer {
  color: @color-font-white;
  background-color: @color-back-black-01;
}

.page-footer__wrapper {
  @media (min-width: @xxl-width) {
    display: grid;
    grid-template-columns: auto 1fr;
    .wrapper();
  }
}

.page-footer__menu-block {
  position: relative;

  display: grid;
  gap: 40px;

  padding: 55px @sm-padding 55px;

  .border-decor-triangle--bottom(6px, 6px, 1px, @color-dark-orange);

  font-family: @font-footer;

  @media (min-width: @md-width) {
    grid-template-columns: min-content 1fr;
    gap: 24px 110px;

    padding: 70px @sm-padding;
  }

  @media (min-width: @xxl-width) {
    grid-template-columns: max-content 1fr;
    gap: 52px 71px;
    padding: 139px @xxl-padding 120px 0;

    .border-decor-triangle--right(6px, 6px, 1px, @color-dark-orange);
  }
}

.page-footer__logo {
  width: 276px;
  height: 95px;

  .interactive-styles-custom();

  @media (min-width: @md-width) {
    grid-row: 1 / 2;
    grid-column: 1 / 2;
  }

  @media (min-width: @xxl-width) {
    width: 194px;
    height: 67px;
  }
}

.page-footer__logo img {
  width: 100%;
  height: 100%;
}

.page-footer__policy {
  display: none;
  width: 240px;

  font-size: 14px;
  font-weight: 400;
  line-height: 21px;

  .interactive-styles();

  @media (min-width: @md-width) {
    grid-row: 2 / 3;
    align-self: end;

    display: block;

    font-weight: 500;
  }
}

.page-footer__site-list {
  width: min-content;
  column-count: 2;
  column-gap: 65px;

  font-size: 17px;
  font-weight: 500;
  line-height: 23px;

  @media (min-width: @md-width) {
    grid-row: 1 / 3;
    grid-column: 2 / 3;

    column-gap: 80px;

    font-size: 20px;
    line-height: 26px;
  }
}

.page-footer__site-list li {
  width: min-content;
  margin-bottom: 30.5px;
}

.page-footer__contacts {
  padding: 65px @sm-padding 60px;

  @media (min-width: @md-width) {
    padding: 70px @sm-padding;
  }

  @media (min-width: @xxl-width) {
    padding: 130px 0 130px @xxl-padding ;
  }
}
